      this.viewDiaryModal = true;
    },
    modalClose(refreshYn) {
      if(this.modalOn) {
        this.modalOn = false;
        this.viewDiaryModal = false;
        this.index = null;
      } 
      console.log('refreshYn : ', refreshYn);
      // if(refreshYn) {
      //   this.$emit('setMyPlantClose', this.cltvtId);
      // }
    },
    getMyPlantDiary(pCltvtId, count){
      this.imgUrlArr = [];
      // https://velog.io/@lgj9172/Axios%EB%A1%9C-%EB%B0%94%EC%9D%B4%EB%84%88%EB%A6%AC%ED%8C%8C%EC%9D%BC-%EB%8B%A4%EC%9A%B4%EB%A1%9C%EB%93%9C-%ED%95%98%EA%B8%B0
      const config = {
            method: "POST",
            url: "/api/plantShowImage",
            responseType: "json",
            data: { cltvtId: pCltvtId, pageNumber: count },
        };

      var fileArr = []; 
      var imgDiaryCntnts;
      var respJson;
      var buffer;
      var recrd_dttm; 
      var daysSince;
      var daysSinceSow;
      var fileName;
      var reactCnt;

      axios(config)
        .then((response) => {
          respJson = response.data;

          for(var i in respJson) {
            fileArr[i] = respJson[i].file;
            imgDiaryCntnts = respJson[i].imgDiaryCntnts;
            recrd_dttm = respJson[i].recrd_dttm;
            daysSince = respJson[i].days_since;
            daysSinceSow = respJson[i].days_since_sow;
            fileName = respJson[i].fileName;
            reactCnt = respJson[i].reactCnt;

            ///// base64 -> ArrayBuffer -> Blob -> ObjectUrl
            // 1. get buffer(file obj)
            buffer = fileArr[i].data;  // filesArr :  {type: 'Buffer', data: Array(515059)}
            
            // 2. buffer to blob
            const blob = new Blob([new Uint8Array(buffer)]);

            // 3. blob -> ObjectUrl
            const fileObjectUrl = window.URL.createObjectURL(blob); //revokeObjectURL???

            this.imgUrlArr[i] = {
                                cltvt_id:respJson[i].cltvt_id
                                , cltvt_act_cd:respJson[i].cltvt_act_cd
                                , recrd_dttm:recrd_dttm
                                , daysSince:daysSince
                                , daysSinceSow:daysSinceSow
                                , fileName: fileName
                                , fileObjectUrl:fileObjectUrl
                                , imgDiaryCntnts:imgDiaryCntnts
                                , reactCnt:reactCnt
                              };
          }
        this.diaryCnt = this.imgUrlArr.length;

        })
        .catch((error) => {
          console.log(error);
        });
    },
    handleScroll() {
      // console.log('handleScroll : this.cltvtId : ', this.cltvtId);
      // console.log(this.$refs.scrollcheck.scrollHeight, ' / ', window.innerHeight, ' / ', window.scrollY);
      // console.log(window.innerHeight, ' / ', this.$refs.scrollcheck.offsetTop, ' / ', window.scrollY);
      // console.log((window.innerHeight + this.$refs.scrollcheck.offsetTop)-window.scrollY);
      // console.log((((window.innerHeight + window.scrollY)-this.$refs.scrollcheck.scrollHeight) % 1000) == 1);


      this.locate = ((window.innerHeight + window.scrollY)-this.$refs.scrollcheck.scrollHeight);
      // console.log('this.locate=', this.locate, ' / ', 'this.locateMax=', this.locateMax);

      var test = Math.round(this.locate / 900 * 100) / 100
      var test2 = Math.round(this.locate / 900)
      // console.log('test=', test, 'test2=', test2, 'test-test2=', test-test2);

      // if (this.$refs.scrollcheck.scrollHeight - (window.innerHeight + window.scrollY) < 1000){
      if (test-test2 > 0 && this.scrollChkAct && this.locate > this.locateMax) {
        this.pageNumber += 1; // 보여질 영화의 개수를 추가한다.
        this.scrollChkAct = false;
        this.locateMax = this.locate;
      } else if (test-test2 < 0 && this.locate < this.locateMax) {
        this.scrollChkAct = true;
      }
      // if (test< 1)
      //   this.scrollChkAct = true;
        
    },
    beforeDestroy() {
      // window.removeEventListener('scroll', this.handleScroll);
    },
    delDiaryConfirm(){
