    delMyPlantImage() {
      var imageItem = this.imgItem;
      
      // 1. delete File
      // 2. delete DB data
      axios
          .post("/api/file/deleteFile", { dml: 'delete'
                                          , cltvtId: imageItem.cltvt_id
                                          , cltvtActCd: imageItem.cltvt_act_cd
                                          , recrdDttm: imageItem.recrd_dttm
                                          , fileName: imageItem.fileName
                                        })
          .then((response) => {
              console.log(response.status);
              alert('반려식물 일기가 삭제되었습니다.');
          })
          .catch((err) => {
              console.log("delete Image error");
              console.log(err);
          });

      this.modalClose(true);
      
    },
    // onFileSelected: function (e) {
    //   // 1. 첨부할 파일을 가져옴.
    //   this.selectedFile = e.target.files[0];
    //   ///// validation (이미지인지? 사이즈 제한? )

