<template>
  <div>
    <img :src="require(`@/assets/images/common/div_fill.jpg`)">  
    <div style="display: flex; justify-content: center;">
      <table style="border: 1px solid lightgray; width: 95%;">
        <tr style="height: 2rem;">
          <td style="width: 5%; text-align: center; border: 1px solid lightgray; background-color: #F7F7F7; font-weight: bold;">
            생육온도
          </td>
          <td style="padding-left: 0.5rem; background-color: white;">
            <div class="keyword_list">
              <button class="button_temperature_item" v-on:click="this.$emit('sendTmprStnd', 'L');" style="border-color: #77A8CC;">
                <div style="color: #77A8CC">🌡&nbsp;{{ bottomTmprStd }}~{{ lowerTmprStd }}°C</div>
              </button>
              <button class="button_temperature_item" v-on:click="this.$emit('sendTmprStnd', 'M');" style="border-color: #E7CC5A;">
                <div type="text" style="color: #E7CC5A">🌡&nbsp;{{ lowerTmprStd }}~{{ middleTmprStd }}°C</div>
              </button>
              <button class="button_temperature_item" v-on:click="this.$emit('sendTmprStnd', 'H');" style="border-color: #EA5D3E;">
                <div style="color: #EA5D3E">🌡&nbsp;{{ middleTmprStd }}~{{ upperTmprStd }}°C</div>
              </button>
            </div>
          </td>
          <td style="width: 5%; text-align: center; border: 1px solid lightgray; background-color: #F7F7F7; font-weight: bold;">
            빛
          </td>
          <td style="padding-left: 0.5rem; background-color: white;">
            <div class="keyword_list">
              <div class="button_light_item" v-on:click="this.$emit('sendLightStnd', '1');">
                <div style="color: #EA5D3E">●&nbsp;양지</div>
              </div>
              <div class="button_light_item" v-on:click="this.$emit('sendLightStnd', '2');">
                <div style="color: #EA5D3E">◐&nbsp;반양지</div>
              </div>
              <div class="button_light_item" v-on:click="this.$emit('sendLightStnd', '3');">
                <div style="color: #77A8CC">◐&nbsp;반음지</div>
              </div>
              <div class="button_light_item" v-on:click="this.$emit('sendLightStnd', '4');">
                <div style="color: #77A8CC">●&nbsp;음지</div>
              </div>
            </div>
          </td>
        </tr>
        <tr style="height: 2rem;">
          <td style="width: 5%; text-align: center; border: 1px solid lightgray; background-color: #F7F7F7; font-weight: bold;">토양습도</td>
          <td style="padding-left: 0.5rem; background-color: white;">1
          </td>
          <td style="width: 5%; text-align: center; border: 1px solid lightgray; background-color: #F7F7F7; font-weight: bold;">공중습도</td>
          <td style="padding-left: 0.5rem; background-color: white;">2
          </td>
        </tr>
        <tr style="">
          <td style="width: 5%; text-align: center; border: 1px solid lightgray; background-color: #F7F7F7; font-weight: bold;">키워드</td>
          <td style="padding-left: 0.5rem; background-color: white;" colspan="5">
            <div class="keyword_list" >
              <button class="button_keyword_item" 
                      v-for="(keywordPlant,id) in this.keywordList" :key="id" 
                      v-on:click="this.$emit('sendKeyword', keywordPlant);">
                {{ keywordPlant.plnt_keyword_vw }}
              </button>
            </div>
          </td>
        </tr>
      </table>
    </div>
    <div style="display: flex; justify-content: center; margin-top: 1rem;">
      <input type="text" placeholder="식물명을 검색하세요." 
          v-model="plantSearchWord"
          @keypress="pressEnterOnSearchFunc($event);"
          style="border:0px solid gray; width:50%; height:2rem; padding-left:1rem; padding-right:1rem;" />
        <button  utton class="button_basic" v-on:click="getPlantSearchList(plantSearchWord)">검색</button>
      <!-- <div>
        <div v-for="(row, rid) in dataPlantSearchResult" :key="rid">
          <tr>
            <td style="width: 5%;"> 
              <div>{{ rid }}</div>
            </td>
            <td style="width: 95%;">
              {{ dataPlantSearchResult[rid].plnt_nm }}
            </td>
          </tr>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
const axios = require("axios");
export default {
  props: ['plantList', 'keywordList'], 
  components: {
  },
  data() {
    return {
      plantSearchWord: '',
      dataPlantSearchResult : [], 
      showMsgYn : false,
      upperTmprStd: 35,
      middleTmprStd: 25,
      lowerTmprStd: 15, 
      bottomTmprStd: 10,
    };
  },
  created() {
  },
  updated() {
  },
  mounted() {

  },
  methods: {
    pressEnterOnSearchFunc(event){
      if(event.keyCode === 13){
          // console.log(event.target.value)
          this.getPlantSearchList(event.target.value);
      }
    },
    getPlantSearchList(searchWord) {
      if (searchWord != '') {
        axios
          .post("/api/plant", {contentsId: 31, searchWord: searchWord})
          // axios.post(this.router, resultJsonImp)
          .then((response) => {
            this.resultJson = response.data;
            this.resultArr = this.resultJson[0].get_result_json;

            if(this.resultArr != undefined) {  // 결과값이 있을 때
              this.dataPlantSearchResult = this.resultArr;
              this.resultKey = Object.keys(this.resultArr[0]);
              // delete this.resultKey[2];
              // console.log('getPlantList :: ', this.dataPlantSearchResult);

              this.$emit('setSearchResult', this.dataPlantSearchResult);
            } else {
              this.dataPlantSearchResult = [];
              this.$emit('setSearchResult', this.dataPlantSearchResult);
            }
          })
          .catch((error) => {
            console.log(error);
          });

      } else {
        alert('식물명 검색어를 입력하세요.');
      }

      // this.dataPlantSearchResult = '';
      this.showMsgYn = true;
    },
  },
};
</script>

<style>
input {
  width: 70%;
  height: 3rem;
  font-size: 1rem;
  border: 0;
  border-radius: 5px;
  outline: none;
  /* padding-left: 10px; */
  background-color: rgb(233, 233, 233);
}
</style>