<template>
  <div class="contents_title">
    <!-- <h3>{{ plantInfo.plnt_nm }} Planground</h3> -->
    <h3>작물별온도</h3>
  </div>
  <div class="contents_container">
    <div class="contents_div_2">
      <div>
      * 날씨 data 출처 : https://data.kma.go.kr/data/grnd/selectAsosRltmList.do?pgmNo=36<br>
      * 측정 지점 위.경도 : https://data.kma.go.kr/tmeta/stn/selectStnList.do?pgmNo=123
      </div>
      <div style="border: 0px; height: 50px overflow-y: scroll;">
        <table>
          <tr>
            <div class="contents_div_2">
              <td style="border: 0px solid orange; width:85%;">
                <table>
                  <tr style="border: 0px solid green; width:100%; vertical-align : middle;">지역선택</tr>
                  <tr style="border: 0px solid blue;">
                    <div style="border: 0px solid red; width:100%; height:10rem; overflow-y: scroll;">
                      <td style="border: 0px solid red; vertical-align:top; width:5%" v-for="item in sidoJson" v-bind:key="item">
                        <tr>{{ item.sido }}</tr>
                        <tr style="text-align: left;" v-for="subitem in item.sigungu" v-bind:key="subitem">
                          <input type="radio" v-bind:value="subitem.sigungu_cd" name="mySigungu" v-model="sigunguCd"> {{ subitem.sigungu_nm }} 
                        </tr>
                      </td>
                    </div>
                  </tr>
                </table>
              </td>
            </div>
            <div class="contents_div_2">
              <td style="width:15%;">
                <tr style="width:100px; height:2rem; text-align : center; vertical-align : middle;">작물선택</tr>
                <tr style="text-align: left; margin-left: 10px;">
                  <div style="height:10rem; overflow-y: scroll;">
                    <div v-for="item in cropJson" v-bind:key="item.crp_id">
                      <input type="checkbox" v-bind:value="item.crp_id" v-model="cropChecked"/>{{ item.crp_nm }}
                    </div>
                  </div>
                </tr>
              </td>
            </div>
          </tr>
          <tr style="text-align : center; vertical-align : middle;">
            <button v-on:click="getWeather(this.sigunguCd)"> 보기</button>
          </tr>
        </table>
      </div>
    </div>
    <div class="contents_div_2">
      <div class="chart_line">
        <LineChart v-if="loaded" 
                  :chartData="chartData" 
                  :chartOptions="chartOptions" 
          />
        <!-- <BarChart v-if="loaded" 
                  :chartData="chartData" 
                  :chartOptions="chartOptions" 
          /> -->
      </div>
    </div>
  </div>
  <div class="botton_box">
    <!-- <div class="right_side_btn" v-on:click="setMyPlantOpen('A');" style="background-color:lightgray;">식물추가/삭제</div>
    <div class="right_side_btn" v-on:click="setMyPlantOpen('B');">재배기록</div>
    <div class="right_side_btn" v-on:click="setMyPlantOpen('C');">육아일기</div>
    <div class="right_side_btn" v-on:click="setMyPlantOpen('D');">문의하기</div> -->
    
    <!-- <div class="right_side_btn" v-on:click="scrollToSection(0);">반려식물기본</div> -->
    <a href="#section1">
      <div class="right_side_btn">작물별온도</div>
    </a>
    <div id="myBtn" class="back_to_top_btn" v-on:click="topFunction();">↑</div>
    <!-- <div class="right_side_btn" id="myBtn" @click="this.topFunction();">Top</div> -->
  </div>
</template>

<script>
// import BarChart from '../Charts/BarChart'
// import LineChart from '../Charts/LineChart.vue'

const axios = require("axios");

export default {
  // name: 'App',
  components: { 
          // BarChart, 
          // LineChart 
        },
  data() {
    
    return {
      cropJson: null,
      sidoJson: null,
      sigunguCd: '108',   // default 서울
      cropChecked:[1], // default

      // chart
      // chartData: {
      //     labels: [ 'January', 'February', 'March' ],
      //     datasets: [ { data: [40, 20, 12] } ]
      // },
      loaded: false,
      chartData: null,
    }
  },
  created() {
  },
  beforeMount() {
  },
  async mounted() {
    this.getSidoList();
    this.getCropList();
    this.getWeather();
  },
  methods:{
    async getSidoList(){
      var resultJson = {};
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 19})
        .then((response) => {
          resultJson = response.data;
          this.sidoJson = resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.sidoJson[0]);  // sido, sinungu
        })
        .catch((error) => {
          console.log(error);
        });
    },

    async getCropList(){
      // crop list
      var resultJson = {};
      await axios
          .post("/api/lineChartDb", {contentsId: 17})
          .then((response) => {
            resultJson = response.data;
            this.cropJson = resultJson[0].get_result_json;
          })
          .catch((error) => {
          console.log(error);
          });
    },

    async getWeather(){
      var resultJson = {};
      var crpIdList = '';
      crpIdList = '\'';

      for (var i in this.cropChecked) {
            if (this.cropChecked[i]) {
              console.log(this.cropChecked[i]);
              crpIdList = crpIdList + '' + this.cropChecked[i] + ',';
            }
          }
      crpIdList = crpIdList.slice(0, -1);
      crpIdList = crpIdList + '\'';
      
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 18, sigungu_cd: this.sigunguCd, crp_id_list: crpIdList })
        .then((response) => {
          resultJson = response.data[0].get_result_json;

          this.chartData = { labels: resultJson.lables, datasets: resultJson.datasets };
          this.chartOptions = { responsive: true, maintainAspectRatio: true };

          this.loaded = true;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
}
</script>
