<template>
  <!-- 식물 기본 start -->
  <!-- <div class="contents_title">
    <h3 id="section1">PLANT_MAS All</h3>
  </div> -->
  <!-- <div class="contents_container" style="height: 35rem; overflow-x: scroll;"> -->
  <div >
    <!-- plant list all start -->
    <!-- {{ dataPlantList }} -->
    <table class="table_info" style="border: 1px solid lightgray;">
      <tr style="border: 1px solid lightgray; background-color: rgb(235, 235, 235);">
        <td v-for="(item,id) in resultKey" :key="id" style="border: 1px solid lightgray;">
          {{item}}
        </td>
      </tr>
      <tr class="tr_info" v-for="(item,id) in dataPlantList" :key="id" style="border: 1px solid lightgray; height: 1rem;">
        <td v-for="(subitem, subid) in item" :key="subid" style="text-overflow: ellipsis; overflow: hidden; white-space: nowrap; max-width: 10rem; border: 1px solid lightgray;">
          <div v-if="Object.keys(dataPlantList[0]) == 'plnt_det_info'" v-on:click="alert('check')">2</div>
          <div v-else>{{ subitem }}</div>
          <!-- <div v-if="typeof subitem != 'object'">{{ subitem }}</div>  -->
          <!-- {{ typeof subitem }} -->
        </td>
      </tr>
    </table>
    <!-- plant list all end -->
  </div>
</template>
  
<script>
const axios = require("axios");

export default {
  components: {},
  data() {
    return {
      dataPlantList: [],
      resultArr: [],
      resultKey: [],
      arrMemo: [],
      selectCrpId: null,
      selectCrpNm: null,
      plntMasInfo: [],
      plntMasInfoTmp: [],
      crpDetailArr: [],
      crpDetailKey: [],
      showModal: false,
      crpAddYn: false,

      crpDetailValue: [], 
      plantInfoTabGbn: 'A',
    };
  },
  created() {
    this.getPlantMasList();
  },
  mounted() {
  },
  methods: {
    getTaxonomyInfo () {
      alert('Modal Taxonomy');
    },
    getPlantMasList() {
      axios
        .post("/api/plant", {contentsId: 17})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.dataPlantList = this.resultArr;
          this.resultKey = Object.keys(this.resultArr[0]);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
  
