    //           console.log('this.menuAdminItem ::: ', this.menuAdminItem);
    //           console.log('this.menuItem ::: ', this.menuItem);

    //       })
    //       .catch((error) => {
    //           console.log(error);
    //       });
    //   },
    async getMenuAuth() {
      var resultJson;

      resultJson = this.menuItemData;

      this.menuAdminItem = resultJson[0][0].result_json;
      this.menuItem = resultJson[0][1].result_json;
    },
