<template>
  <!-- 식물 기본 start -->
  <div class="contents_title">
    <h3 id="section1">PLANT_MAS</h3>
  </div>
  <div class="contents_container" style="border: 0px solid hotpink;">
    <!-- 1. 작물 추가 start -->
    <div class="contents_div_5" style="border: 0px solid hotpink;">
      <div class="contents_div_5_title">
        작물 추가
        <div class="contents_add" v-on:click="setCropMasMode();">
        </div>
      </div>
      <!-- <PlantSearch></PlantSearch> -->
      <div style="width: 95%; height: 30rem; overflow-x: hidden; overflow-y: scroll; margin: 1rem;">
        <div class="contents_container">
          <button class="button_basic" v-on:click="sortPlantList('N');">최신정렬</button>
          <button class="button_basic" v-on:click="sortPlantList('Y');">이름정렬</button>
        </div>
        <div class="contents_container">
          <div>
            <div class="list_btn" v-for="(row, rid) in dataPlantList" :key="rid" v-on:click="getPlantMas(dataPlantList[rid].plnt_id);">
              {{ dataPlantList[rid].plnt_nm }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 1. 작물 추가 end -->

    <!-- 2. 작물 정보 start -->
    <div class="contents_div_4">
      <!-- 작물 정보 기본 start -->
      <div class="contents_div_4_title">
        <div v-on:click="plantInfoTabGbn='A'">작물 정보</div>
        <div>
          <div v-if="crpAddYn" style="float:left; margin-left: 3rem;">
            신규 작물 등록/수정
            <button v-on:click='editPlantMas("insert");'>저장</button>
          </div>
          <div v-else style="float:left; margin-left: 3rem;">
            {{ selectCrpNm }} (crp_id : {{ selectCrpId }})
            <button v-on:click='editPlantMas("update");'>수정</button>
            <button v-on:click='editPlantMas("delete");'>삭제</button>
          </div>
        </div>
      </div>
      <!-- 작물 정보 기본 end -->
      <!-- 작물 추가/수정/삭제 모드 start -->
      <div>
        <div style="width: 95%; height: 30rem; overflow-x: hidden; overflow-y: scroll; margin: 1rem;">
          <table>
            <div v-for="(row, rid) in plntMasInfo" :key="rid">
              <tr>
                <td style="width: 65%;"> 
                  <div v-if="rid === 'txnmy_id'">{{ rid }} <button class="button_basic" v-on:click="getTaxonomyInfo();">선택</button></div>
                  <div v-else-if="rid === 'file_url'">{{ rid }} [plant##_01.jpg] [plantNoImg.jpg]</div>
                  <div v-else>{{ rid }}</div>
                </td>
                <td style="width: 35%;">
                  <input type="text" class="input_text" v-model="plntMasInfo[rid]" />
                </td>
              </tr>
            </div>
          </table>
        </div>
      </div>
      <!-- 작물 추가/수정/삭제 모드 end -->
    </div>
    <!-- 2. 작물 정보 end -->
  </div>
  <!-- 식물 기본 end -->
</template>
  
<script>
const axios = require("axios");
// import PlantSearch from '../plant/PlantSearch.vue';

export default {
  // components: {PlantSearch},
  data() {
    return {
      dataPlantList: [],
      resultArr: [],
      resultKey: [],
      arrMemo: [],
      selectCrpId: null,
      selectCrpNm: null,
      plntMasInfo: [],
      plntMasInfoTmp: [],
      crpDetailArr: [],
      crpDetailKey: [],
      showModal: false,
      crpAddYn: false,

      crpDetailValue: [], 
      plantInfoTabGbn: 'A',
    };
  },
  created() {
    this.getPlantMasList();
    this.getPlantMas(1);
  },
  mounted() {
  },
  methods: {
    getTaxonomyInfo () {
      alert('Modal Taxonomy');
    },
    sortPlantList(sortYn) {
      if(sortYn == 'Y') {
        // order by age asc
          this.dataPlantList = [...this.resultArr].sort((a, b) => {
            if (a.plnt_nm < b.plnt_nm) return -1;
            if (a.plnt_nm > b.plnt_nm) return 1;

            return 0;
        });
      } else {
        this.dataPlantList = [...this.resultArr];
      }
      // console.log('this.dataPlantList ::: ', this.dataPlantList);
    },
    getPlantMasList() {
      axios
        .post("/api/plant", {contentsId: 17})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.dataPlantList = this.resultArr;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
          console.log('getPlantMasList :: this.dataPlantList : ', this.dataPlantList);
        })
        .catch((error) => {
          console.log(error);
        });
        console.log('this.resultJson ::: ', this.resultJson);
    },
    getPlantMas(plntId) {
      var resultJson = null;
      var crpDetailArr = [];
      axios
        .post("/api/plant", {contentsId: 22, plntId: plntId})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          resultJson = response.data;
          crpDetailArr = resultJson[0].get_result_json;
          this.plntMasInfo = crpDetailArr[0];
          console.log('this.plntMasInfo : ', this.plntMasInfo);
          
          this.selectCrpId = plntId;
          this.selectCrpNm = this.plntMasInfo.crp_nm;
        })
        .catch((error) => {
          console.log(error);
        });
      this.crpAddYn=false;
    },
    editPlantMas (dmlGbn) {
      console.log('editPlantMas==================');
      console.log('plntMasInfo : ', this.plntMasInfo);

      var resultMsg;
      axios
        .post("/api/plant", { dml: dmlGbn, plntId: this.selectCrpId, plntMasInfo: this.plntMasInfo})
        // .post("/api/plant", { dml: 'insert', tabNm: 'crop_mas_backup', cltvtActJsonArray: this.resultArr})
        .then((response) => {
          resultMsg = response.data.result;
          console.log('resultMsg : ',resultMsg);
        })
        .catch((error) => {
          console.log(error);
        });   
      
      window.location.href = '/test5';
    },
    setCropMasMode() {
      this.crpAddYn = true;

      var str = '';
      var cnt = Object.keys(this.plntMasInfo).length;
      var idx = 0;
      str = '{';
      var value = null;

      for (var key in this.plntMasInfo) {
        if ( !this.crpAddYn ) {
          value = this.plntMasInfo[key];
        }

        if(typeof(value) == 'string') {
          str += '"' + key + '" : "' + value + '"';
        } else {
          str += '"' + key + '" : ' + value;
        }
        // console.log('idx : ', idx, ' / cnt : ', cnt);
        if ( idx < cnt-1 ){
          str += ', ';
        }
        idx = idx+1;
      }
      
      str += '}'
      console.log('str : ' , str);

      this.plntMasInfo = JSON.parse(str);

    },
    
  },
};
</script>
  
<style>
</style>
