<script>
import plantListJson from '../assets/json/plantList.json'; // select get_result_json(17); plant 기준

import BarChart from './chart/BarChart.vue';
import keywordPlant from '../assets/json/keywordPlant.json'; // select get_result_json(27);   keyword 기준

import DoughnutChart from './chart/DoughnutChart.vue';
import keywordCtgrztn from '../assets/json/keywordCtgrztn.json'; // select get_result_json(28);

import healingActivityJson from '../assets/json/healingActivityJson.json'; // select get_result_json(9);

export default {
  props: {
  },
  components: {
    DoughnutChart,
    BarChart,
  },
  created() {
    this.setDoughnutChartData();
    this.setBarChartData();
  },
  data() {
    return {
      showRouterViewYn: false,
      barChartData: {},
      barChartOptions : {},
      doughnutChartData: {},
