      dataKeywordPlantJson : keywordPlant,
      backgroundImgUrl: require(`@/assets/images/dashboard/dashboard_main.jpg`)
    };
  },
  methods: {
    setDoughnutChartData() {
      var chartDataLabels = [];
      var chartDataDatas = [];
      var chartBackgroundColor = [];

      for(var i = 0; i < keywordCtgrztn.length; i++) {
        chartDataLabels.push(keywordCtgrztn[i].cdtyp_nm);
        chartDataDatas.push(keywordCtgrztn[i].plnt_cnt);
        chartBackgroundColor.push(keywordCtgrztn[i].cdtyp_cd_color);
      }
      this.doughnutChartData = {
        labels:chartDataLabels, 
        datasets:[
          {
            label: '키워드분류건수',
            data: chartDataDatas, 
            backgroundColor: chartBackgroundColor,
            // borderColor: [
            //   'rgba(255, 99, 132, 1)',
            //   'rgba(54, 162, 235, 1)'
            // ],
            borderWidth: 1
                  }
                ]
        },
      this.doughnutChartOptions = {
        indexAxis: 'y', //수평차트 만들기
        scales : {
                x: {
                  // beginAtZero: true
                  // min: 0,
                  // max: 100
                }
              },
        responsive : true,
        animation: {
            duration: 0
        },
        maintainAspectRatio :false,   // 비율고정 해제
      }
      // this.width = 50,
      // this.height = 10
    },
    setBarChartData() {
      var chartDataLabels = [];
      var chartDataDatas = [];
      var chartBackgroundColor = [];

      for(var i = 0; i < keywordPlant.length; i++) {
        chartDataLabels.push(keywordPlant[i].plnt_keyword_vw);
        chartDataDatas.push(keywordPlant[i].plnt_cnt);
        chartBackgroundColor.push(keywordPlant[i].cdtyp_cd_color);
      }
      this.barChartData = {
        labels:chartDataLabels, 
        datasets:[{
          // barPercentage: 0.5,
          // barThickness: 6,
          // maxBarThickness: 8,
          // minBarLength: 2,

          label: '식물등록건수',
          data:chartDataDatas, 
          backgroundColor: chartBackgroundColor,
          // borderColor: [
          //   'rgba(255, 99, 132, 1)',
          //   'rgba(54, 162, 235, 1)'
          // ],
          borderWidth: 1,
          // *** datalabel option 지정 
          datalabels: {
            display: true, // default는 true, false 로 하면 값을 숨길 수 있다.
            align: "right",
            anchor: "end",
            color: "#898989",
            borderWidth: 2,
            padding: {
              bottom: 0,
              left: 20,
            },
          },
        }]
      },
      this.barChartOptions = {
        indexAxis: 'y', //수평차트 만들기
        scales : {
                x: {
                  // beginAtZero: true
                  // min: 0,
                  // max: 100
                },
                y: {
                  // beginAtZero: true
                }
              },
        // responsive : true,
        // animation: {
        //     duration: 0
        // },
        maintainAspectRatio :false,   // 비율고정 해제
      }
      // this.width = 400,
      // this.height = 500
    },
    goToContent () {
      console.log("go To Content..");
      // alert('게시글 내용으로 이동합니다.');
      window.open('/plantingBoard', "_blank");


      // $router.push(url + '/author');   // script 태그 내부에서는 $router is not defined
      // location.href = 'plantingBoard';
      
    }
