<template>
  <div class="contents_title">
    <h3 id="section1">TAXONOMY_MAS</h3>
  </div>
  <div class="contents_container" style="border: 0px solid hotpink;">
    <!-- 1. 추가 start -->
    <div class="contents_div_4" style="border: 0px solid hotpink;">
      <div class="contents_div_5_title">
        생태 분류 추가 (최소 종단위)
        <div class="contents_add" v-on:click="setTaxonomyMasMode();">
        </div>
      </div>
      <div style="width:90%; height: 30rem; overflow-x: hidden; overflow-y: scroll;">
        <div class="contents_container">
          txnmy_id : 
          <input type="text" style="width: 10rem; height: 2rem; background-color: lightgreen; margin: 0.2rem; padding: 0.2rem;" 
                  v-model="taxonomySearchId" />
          <button v-on:click="getTaxonomyDetail(taxonomySearchId)">검색</button>
        </div>
        <div class="contents_container">
          과속종 : 
          <input type="text" style="width: 10rem; height: 2rem; background-color: lightgreen; margin: 0.2rem; padding: 0.2rem;" 
                  v-model="taxonomySearchWord" />
          <button v-on:click="getTaxonomySearchList(taxonomySearchWord)">검색</button>
        </div>
        <div class="contents_container">
          <div v-if="dataTaxonomySearchResult != undefined">
            <div class="list_btn" v-for="(row, rid) in dataTaxonomySearchResult" :key="rid" 
                  v-on:click="getTaxonomyDetail(dataTaxonomySearchResult[rid].txnmy_id);">
              {{ dataTaxonomySearchResult[rid].fmly_kor_nm }} / 
              {{ dataTaxonomySearchResult[rid].gns_kor_nm }} / 
              {{ dataTaxonomySearchResult[rid].spcs_kor_nm }}
            </div>
          </div>
          <div v-else>
            검색 결과가 없습니다.
          </div>
        </div>
      </div>
    </div>
    <!-- 1. 추가 end -->


    <!-- 2. 생태분류 정보 start -->
    <div class="contents_div_4">
      <!-- 생태분류 정보 기본 start -->
      <div class="contents_div_4_title">
        <div v-on:click="plantInfoTabGbn='A'">작물 정보</div>
        <div>
          <div v-if="crpAddYn" style="float:left; margin-left: 3rem;">
            신규 작물 등록/수정
            <button v-on:click='editPlantMas("insert");'>저장</button>
          </div>
          <div v-else style="float:left; margin-left: 3rem;">
            {{ selectCrpNm }} (txnm_id : {{ txnmyInfo.txnmy_id }})
            <button v-on:click='editPlantMas("update");'>수정</button>
            <button v-on:click='editPlantMas("delete");'>삭제</button>
          </div>
        </div>
      </div>
      <!-- 생태분류 정보 기본 end -->
      <!-- 생태분류 추가/수정/삭제 모드 start -->
      <div>
        <div style="width:90%; height: 30rem; overflow-x: hidden; overflow-y: scroll; margin: 1rem;">
          <table>
            <div v-for="(row, rid) in txnmyInfo" :key="rid">
              <tr>
                <td style="width: 65%;"> 
                  <div>{{ rid }}</div>
                </td>
                <td style="width: 35%;">
                  <input type="text" class="input_text" v-model="txnmyInfo[rid]" />
                </td>
              </tr>
            </div>
          </table>
        </div>
      </div>
      <!-- 생태분류 추가/수정/삭제 모드 end -->
    </div>
    <!-- 2. 생태분류 정보 end -->
  </div>
</template>
  
<script>
const axios = require("axios");
export default {
  components: {},
  data() {
    return {
      dataTaxonomySearchResult: [], 
      txnmyInfo: {},
      taxonomySearchWord: '', 
      taxonomySearchId: 0,
      selectTxnmyId: 1, 
      selectTxnmyNm: '', 
    };
  },
  created() {
    this.getTaxonomyDetail(1);
    this.getTaxonomySearchList('천남성과');
  },
  mounted() {
  },
  methods: {
    getTaxonomySearchList(searchWord) {
      if (searchWord != '') {
        console.log('check1 240604');
        axios
          .post("/api/taxonomy", {contentsId: 29, searchWord: searchWord})
          // axios.post(this.router, resultJsonImp)
          .then((response) => {
            this.resultJson = response.data;
            this.resultArr = this.resultJson[0].get_result_json;
            this.dataTaxonomySearchResult = this.resultArr;
            this.resultKey = Object.keys(this.resultArr[0]);
            // delete this.resultKey[2];
            console.log('getTaxonomyMasList :: this.dataTaxonomySearchResult : ', this.dataTaxonomySearchResult);
          })
          .catch((error) => {
            console.log(error);
          });
          console.log('this.resultJson ::: ', this.resultJson);
      } else {
        alert('과/속/종 검색어 입력하세요.');
      }

      this.taxonomySearchId = '';
      
    },
    getTaxonomyDetail(txnmyId) {
      var resultJson = null;
      var txnmyDetailArr = [];
      axios
        .post("/api/taxonomy", {contentsId: 30, txnmyId: txnmyId})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          resultJson = response.data;
          txnmyDetailArr = resultJson[0].get_result_json;
          this.txnmyInfo = txnmyDetailArr[0];
          console.log('this.txnmyInfo : ', this.txnmyInfo);
          
          this.selectTxnmyId = txnmyId;
          this.selectTxnmyNm = this.txnmyInfo.txnmy_nm;
        })
        .catch((error) => {
          console.log(error);
        });
      this.crpAddYn=false;
      
      this.taxonomySearchWord = '';
    },
    setTaxonomyMasMode() {
      this.crpAddYn = true;

      var str = '';
      var cnt = Object.keys(this.txnmyInfo).length;
      var idx = 0;
      str = '{';
      var value = null;

      for (var key in this.txnmyInfo) {
        if ( !this.crpAddYn ) {
          value = this.txnmyInfo[key];
        }

        if(typeof(value) == 'string') {
          str += '"' + key + '" : "' + value + '"';
        } else {
          str += '"' + key + '" : ' + value;
        }
        // console.log('idx : ', idx, ' / cnt : ', cnt);
        if ( idx < cnt-1 ){
          str += ', ';
        }
        idx = idx+1;
      }
      
      str += '}'
      console.log('str : ' , str);

      this.txnmyInfo = JSON.parse(str);

    },
    editPlantMas (dmlGbn) {
      var resultMsg;
      axios
        .post("/api/taxonomy", { dml: dmlGbn, txnmyId: this.selectTxnmyId, txnmyInfo: this.txnmyInfo})
        .then((response) => {
          resultMsg = response.data.result;
          console.log('resultMsg : ',resultMsg);
        })
        .catch((error) => {
          console.log(error);
        });   
      
      window.location.href = '/test5';
    },
  },
};
</script>
  
<style>
</style>
