import { createWebHistory, createRouter } from 'vue-router'

import DashBoard from '../components/DashBoard'

// import HomeMain from '../components/HomeMain'
import IntroMain from '../components/intro/IntroMain'
import IntroCompany from '../components/intro/IntroCompany'


import PlantMain from '../components/plant/PlantMain'
import PlantIntro from '../components/plant/PlantIntro'
import PlantCtgrztn from '../components/plant/PlantCtgrztn'
import PlantCtgrztnApi from '../components/plant/PlantCtgrztnApi'
import PlantKnowledge from '../components/plant/PlantKnowledge'

import MyplantMain from '../components/myplant/MyplantMain'
import MyplantDiary from '../components/myplant/MyplantDiary'
import MyplantDashboard from '../components/myplant/MyplantDashboard'

import CommunityMain from '../components/community/CommunityMain'
import PlantingBoard from '../components/community/PlantingBoard'
import PlantingFeelings from '../components/community/PlantingFeelings'
import CommunityQna from '../components/community/CommunityQna'

import HealingMain from '../components/healing/HealingMain'
import Menu1_1 from '../components/healing/Menu1_1'
import Menu1_2 from '../components/healing/Menu1_2'
import HealingActivity from '../components/healing/HealingActivity'

import AdminMenu from '../components/admin/AdminMenu'
import AdminMain from '../components/admin/AdminMain'
import PlantAll from '../components/admin/PlantAll'
import PlantInsert from '../components/admin/PlantInsert'
import TaxonomyInsert from '../components/admin/TaxonomyInsert'

import VisualizationMain from '../components/visualization/VisualizationMain'

const routes = [
    {
        path: '/',          // 경로
        redirect: '/dashboard',     // 이동할 컴포넌트(router-view 영역에 보여줄 컴포넌트를 지정)
    },
    {
        path: '/dashboard',          // 경로
        name: 'dashboard',           // 해당 경로의 이름(참고)
        component: DashBoard,     // 이동할 컴포넌트(router-view 영역에 보여줄 컴포넌트를 지정)
    },
    // { path: '/homeMain', name: 'homeMain', component: HomeMain },
    { path: '/introMain', name: 'introMain', component: IntroMain },
    { path: '/introCompany', name: 'introCompany', component: IntroCompany },
    
    { path: '/plantMain', name: 'plantMain', component: PlantMain },
    { path: '/plantIntro', name: 'plantIntro', component: PlantIntro },
    { path: '/plantCtgrztn', name: 'plantCtgrztn', component: PlantCtgrztn },
    { path: '/plantCtgrztnApi', name: 'plantCtgrztnApi', component: PlantCtgrztnApi },
    { path: '/plantKnowledge', name: 'plantKnowledge', component: PlantKnowledge },

    { path: '/myplantMain', name: 'MyplantMain', component: MyplantMain },
    { path: '/myplantDiary', name: 'MyplantDiary', component: MyplantDiary },
    { path: '/myplantDashboard', name: 'MyplantDashboard', component: MyplantDashboard },

    { path: '/communityMain', name: 'communityMain', component: CommunityMain },
    { path: '/plantingBoard', name: 'plantingBoard', component: PlantingBoard },
    { path: '/plantingFeelings', name: 'plantingFeelings', component: PlantingFeelings },
    { path: '/communityQna', name: 'CommunityQna', component: CommunityQna },

    { path: '/healingMain', name: 'HealingMain', component: HealingMain },
    { path: '/menu1_1', name: 'Menu1_1', component: Menu1_1 },
    { path: '/menu1_2', name: 'Menu1_2', component: Menu1_2 },
    { path: '/healingActivity', name: 'HealingActivity', component: HealingActivity },

    { path: '/admin/adminMenu', name: 'adminMenu', component: AdminMenu },
    { path: '/admin/adminMain', name: 'adminMain', component: AdminMain },
    { path: '/admin/taxonomyInsert', name: 'taxonomyInsert', component: TaxonomyInsert },
    { path: '/admin/plantInsert', name: 'plantInsert', component: PlantInsert },
    { path: '/admin/plantAll', name: 'plantAll', component: PlantAll },
    
    { path: '/visualizationMain', name: 'VisualizationMain', component: VisualizationMain },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;


