<template>
  <div class="contents_container_inner">
    <!-- <div class="contents_title">
      <h3>힐링 활동을 소개합니다.</h3>
    </div>   -->
    <div class="contents_container_inner" v-if="showContentYn">
      <div class="contents_item_full" style="border-top: 3px solid #9abf7f; border-bottom: 1px solid #9abf7f;">
        <div style="float:left;">{{ healingActivity.title }}</div>
        <div>{{ healingActivity.reg_dt }}</div>
      </div>
      <div class="contents_item_full" style="border: 1px solid #9abf7f;">
        <div class="board_contents" v-html="healingActivity.cntnts.split('\n').join('<br />')" >
        </div>
      </div>
    </div>
    <div class="contents_container_inner" v-if="showContentYn" style="display: flex; justify-content: center; align-items: center;">
      <div class="contents_item_full">
        <button v-on:click="showContentYn = false;">목록</button>
      </div>
    </div>
    <div class="contents_container_inner">
      <div class="contents_item_full">
        <table>
          <tr>
            <th>No.</th><th>제목</th><th>작성자</th><th>날짜</th><th>조회</th>
          </tr>
          <tr style="width:100%;" v-for="healingActivity in displayedPosts" :key="healingActivity.id">
            <td style="width:7%; text-align: center;">
              {{ healingActivity.brd_id }}
            </td>
            <td class="td-content" v-on:click="showContent(healingActivity);">
              {{ healingActivity.title }}
            </td>
            <td style="width:12%; text-align: center;">
              관리자
            </td>
            <td style="width:10%; text-align: center;">
              {{ healingActivity.reg_dt }}
            </td>
            <td style="width:7%; text-align: center;">
              0
            </td>
          </tr>
        </table>
      </div>
      <div class="contents_item_full" style="display: flex; justify-content : center; width:15rem;">
        <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
          <button v-if="currentPage > 1" v-on:click="currentPage--">이전페이지</button>
        </div>
        <div style="width:20%; display: flex; justify-content: center; align-items: center; margin:auto;">
          <span>{{ currentPage }} / {{ totalPages }}</span>
        </div>
        <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
          <button v-if="currentPage < totalPages" v-on:click="currentPage++">다음페이지</button>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);

export default {
  props: [], 
  components: {
  },
  data() {
    return {
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
  },
  methods: {
    showContent(healingActivity) {
      this.healingActivity = healingActivity;
      this.showContentYn = true;
    },
  },
};
</script>