<template>
  <div>
    <img :src="require(`@/assets/images/common/div_fill.jpg`)">  
    <div class="contents_title" v-if="this.setListAll">
      <h3>5월 탄생화</h3>
    </div>
    <div class="contents_container_inner" v-if="this.setListAll">
      <div v-for="(plant, id) in plantList" :key="id">
        <div class="contents_item_list" v-if="id < 5"> 
          <div v-on:click="$emit('plantClicked', plant)" style="border:0px solid red; margin-bottom: 0.4rem;">
            <div v-if="plant.plantImgUrl != ''" >
              <img class="image_card_mini" :src="require(`@/assets/images/${plant.file_url}`)">
            </div>
            <div class="image_card_mini" v-else style="text-align: center;">
              No Images.
            </div>
            <div class="plant_preview_text">
              {{ plant.plnt_nm }}
            </div>
          </div>
          <div class="plant_preview_keyword">
            <div class="plant_preview_keyword_list" v-for="(keywordInfo,id) in plant.kywrd_list" :key="id">
              <div v-on:click="this.$emit('sendKeyword', keywordInfo);">
                {{ keywordInfo.plnt_keyword_vw }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 식물목록 title-->
    <div class="contents_title" v-if="this.setListAll">
      <h3>전체 식물</h3>
    </div>
    <!-- 키워드 선택한 경우 키워드명과 간단한 설명을 명시한다. -->
    <div v-if="this.setListAll==false && this.setListGb=='Keyword'">
      <div class="contents_title" >
        <div style="display: flex; align-items: center;">
          <h3>{{ clickedKeywordInfo.plnt_keyword_vw }} 식물</h3>
        </div>
        <div style="display: flex; align-items: center; margin-left: 1rem; color: #61656A; font-size:0.8rem;
                    text-overflow:ellipsis; 
                    overflow:hidden; 
                    white-space:nowrap; 
                    max-width: 90%;">
          {{clickedKeywordInfo.cd_expln}}
        </div>
      </div>
    </div>
    <div class="contents_title" v-else-if="this.setListAll==false && this.setListGb=='Temperature'">
      <h3>평균 생육적온 
        <text v-if="tmprRange[0] >- 100 && tmprRange[1] < 100">{{ tmprRange[0] }}°C ~ {{ tmprRange[1] }}°C</text>
        <text v-else-if="tmprRange[0] == -100"> {{tmprRange[1]}}°C 이하인</text>
        <text v-else-if="tmprRange[1] == 100"> {{tmprRange[0]}}°C 이상인</text>
      식물</h3>
    </div>
    <!-- <div class="contents_container">
      <div>
        <button class="button_basic" v-on:click="$emit('setAllPlantList', 'N');">기본정렬</button>
        <button class="button_basic" v-on:click="$emit('setAllPlantList', 'Y');">이름정렬</button>
      </div>
    </div> -->
    <!-- 식물 목록 미리보기 -->
    <div class="contents_container_inner">
      <div class="contents_item_list" v-for="(plant,id) in plantList" :key="id">
        <div v-on:click="$emit('plantClicked', plant)" style="border:0px solid red; margin-bottom: 0.4rem;">
          <div v-if="plant.plantImgUrl != ''" >
            <img class="image_card_mini" :src="require(`@/assets/images/${plant.file_url}`)">
          </div>
          <div class="image_card_mini" v-else style="text-align: center;">
            No Images.
          </div>
          <div class="plant_preview_text">
            {{ plant.plnt_nm }}
          </div>
        </div>
        <div class="plant_preview_keyword">
          <div class="plant_preview_keyword_list" v-for="(keywordInfo,id) in plant.kywrd_list" :key="id">
            <!-- <div style="float:left; margin-right: 0.3rem;" v-on:click="this.$emit('sendKeyword', keywordInfo);"> -->
            <div v-on:click="this.$emit('sendKeyword', keywordInfo);">
              {{ keywordInfo.plnt_keyword_vw }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['plantList', 'setListAll', 'clickedKeywordInfo', 'setListGb', 'tmprRange'], 
  components: {
  },
  data() {
    return {
    
    };
  },
  created() {
  },
  mounted() {
  },
  updated() {
  },
  methods: {
  }
};
</script>