<template>
  <!-- 관리자 메뉴 항목 start -->
  <div class="contents_item_medium_right">
    <div>
        관리자 메뉴 목록 (식물, 게시글, ...)
        <router-link :to="{ path: './plantInsert' }">
          <button style="width: 4rem; height: 1.5rem; background-color: lightgreen; border: 1px solid lightgray; border-radius: 10px; 
                        color: #61656A; font-size:0.8rem;">Plant</button>
        </router-link>
        <router-link :to="{ path: './plantInsert' }">
          <button style="width: 4rem; height: 1.5rem; background-color: lightgreen; border: 1px solid lightgray; border-radius: 10px; 
                        color: #61656A; font-size:0.8rem;">Taxonomy</button>
        </router-link>
    </div>
  </div>
  <!-- 관리자 메뉴 항목 end -->
</template>
  
<script>
const axios = require("axios");
export default {
  components: {},
  data() {
    return {
      dataPlantList: [],
      resultArr: [],
      resultKey: [],
      arrMemo: [],
      selectCrpId: null,
      selectCrpNm: null,
      plntMasInfo: [],
      plntMasInfoTmp: [],
      crpDetailArr: [],
      crpDetailKey: [],
      showModal: false,
      crpAddYn: false,

      crpDetailValue: [], 
      plantInfoTabGbn: 'A',
    };
  },
  created() {
    this.getPlantMasList();
    this.getPlantMas(1);
  },
  mounted() {
  },
  methods: {
    setNoImg(){
      alert('set No Image');
    },
    getTaxonomyInfo () {
      alert('Modal Taxonomy');
    },
    sortPlantList(sortYn) {
      if(sortYn == 'Y') {
        // order by age asc
          this.dataPlantList = [...this.resultArr].sort((a, b) => {
            if (a.plnt_nm < b.plnt_nm) return -1;
            if (a.plnt_nm > b.plnt_nm) return 1;

            return 0;
        });
      } else {
        this.dataPlantList = [...this.resultArr];
      }
      console.log('this.dataPlantList ::: ', this.dataPlantList);
    },
    getPlantMasList() {
      axios
        .post("/api/plant", {contentsId: 17})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.dataPlantList = this.resultArr;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
          console.log('getPlantMasList :: this.dataPlantList : ', this.dataPlantList);
        })
        .catch((error) => {
          console.log(error);
        });
        console.log('this.resultJson ::: ', this.resultJson);
    },
    getPlantMas(plntId) {
      var resultJson = null;
      var crpDetailArr = [];
      axios
        .post("/api/plant", {contentsId: 22, plntId: plntId})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          resultJson = response.data;
          crpDetailArr = resultJson[0].get_result_json;
          this.plntMasInfo = crpDetailArr[0];
          console.log('this.plntMasInfo : ', this.plntMasInfo);
          
          this.selectCrpId = plntId;
          this.selectCrpNm = this.plntMasInfo.crp_nm;
        })
        .catch((error) => {
          console.log(error);
        });
      this.crpAddYn=false;
    },
    editPlantMas (dmlGbn) {
      console.log('editPlantMas==================');
      console.log('plntMasInfo : ', this.plntMasInfo);

      var resultMsg;
      axios
        .post("/api/plant", { dml: dmlGbn, plntId: this.selectCrpId, plntMasInfo: this.plntMasInfo})
        // .post("/api/plant", { dml: 'insert', tabNm: 'crop_mas_backup', cltvtActJsonArray: this.resultArr})
        .then((response) => {
          resultMsg = response.data.result;
          console.log('resultMsg : ',resultMsg);
        })
        .catch((error) => {
          console.log(error);
        });   
      
      window.location.href = '/test5';
    },
    setCropMasMode() {
      this.crpAddYn = true;

      var str = '';
      var cnt = Object.keys(this.plntMasInfo).length;
      var idx = 0;
      str = '{';
      var value = null;

      for (var key in this.plntMasInfo) {
        if ( !this.crpAddYn ) {
          value = this.plntMasInfo[key];
        }

        if(typeof(value) == 'string') {
          str += '"' + key + '" : "' + value + '"';
        } else {
          str += '"' + key + '" : ' + value;
        }
        // console.log('idx : ', idx, ' / cnt : ', cnt);
        if ( idx < cnt-1 ){
          str += ', ';
        }
        idx = idx+1;
      }
      
      str += '}'
      console.log('str : ' , str);

      this.plntMasInfo = JSON.parse(str);

    },
    
  },
};
</script>
  
