              // this.file_url = require("~/" + usrId + "/" + "effa00de879308ac5318d8f3bdafb086.jpg");

              // this.file_url = "C:/dev/git/vue/cdn/myplant/1/" + this.myPlantArr[i].file_url;
              
              // this.urlRef = this.myPlantArr[i].ref_url;
              // this.wikiSearchUrl = wikiSearchUrl + this.item_name;
              // console.log('this.days_since_water ::: ', this.days_since_water);

